// pages/products.js
import { useState, useMemo } from "react";
import { useRouter } from "next/router";
import Layout from "../components/Layout";
import { useAuth } from "../context/AuthContext";
import { useInventory } from "../context/InventoryContext";
import WarningIndicator from "../components/WarningIndicator";
import Modal from "../components/Modal";

export default function ProductsPage() {
  const router = useRouter();
  const { user, hasPermission } = useAuth();
  const {
    products,
    setProducts,
    getWarnings,
    printInventoryReport,
  } = useInventory();

  // ÿ®ÿ≠ÿ´ ŸàŸÅŸÑÿ™ÿ±ÿ© Ÿàÿ™ÿ±ÿ™Ÿäÿ®
  const [search, setSearch] = useState("");
  const [categoryFilter, setCategoryFilter] = useState("all");
  const [companyFilter, setCompanyFilter] = useState("all");
  const [sortByName, setSortByName] = useState("asc");
  const [filterLowStock, setFilterLowStock] = useState(false);
  const [filterNearExpiry, setFilterNearExpiry] = useState(false);
  const [filterExpired, setFilterExpired] = useState(false);

  // ÿπÿ±ÿ∂ ÿ™ŸÅÿßÿµŸäŸÑ
  const [showDetails, setShowDetails] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState(null);

  // ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨
  const [showAddModal, setShowAddModal] = useState(false);
  const [newProduct, setNewProduct] = useState({
    name: "",
    sku: "",
    category: "",
    company: "",
    purchasePrice: "",
    price: "",
    quantity: "",
    minQty: 5,
    expiryDate: "",
  });

  if (!hasPermission(["admin", "pharmacist"])) {
    return (
      <div dir="rtl" className="p-6 text-center text-red-600">
        ‚ö†Ô∏è ŸÑÿß ŸäŸÖŸÉŸÜŸÉ ÿØÿÆŸàŸÑ Ÿáÿ∞Ÿá ÿßŸÑÿµŸÅÿ≠ÿ©.
      </div>
    );
  }

  const categories = [
    "all",
    ...new Set(products.map((p) => p.category).filter(Boolean)),
  ];

  const companies = [
    "all",
    ...new Set(products.map((p) => p.company).filter(Boolean)),
  ];

  const filteredProducts = useMemo(() => {
    let result = [...products];

    if (search.trim() !== "") {
      result = result.filter((p) =>
        p.name.toLowerCase().includes(search.toLowerCase())
      );
    }

    if (categoryFilter !== "all") {
      result = result.filter((p) => p.category === categoryFilter);
    }

    if (companyFilter !== "all") {
      result = result.filter((p) => p.company === companyFilter);
    }

    if (filterLowStock) {
      result = result.filter((p) => p.quantity <= p.minQty);
    }

    if (filterNearExpiry) {
      result = result.filter((p) => {
        if (!p.expiryDate) return false;
        const days =
          (new Date(p.expiryDate) - new Date()) /
          (1000 * 60 * 60 * 24);
        return days > 0 && days <= 30;
      });
    }

    if (filterExpired) {
      result = result.filter((p) => {
        if (!p.expiryDate) return false;
        const days =
          (new Date(p.expiryDate) - new Date()) /
          (1000 * 60 * 60 * 24);
        return days < 0;
      });
    }

    result.sort((a, b) => {
      if (sortByName === "asc") return a.name.localeCompare(b.name);
      return b.name.localeCompare(a.name);
    });

    return result;
  }, [
    search,
    categoryFilter,
    companyFilter,
    sortByName,
    filterLowStock,
    filterNearExpiry,
    filterExpired,
    products,
  ]);

  const openDetails = (p) => {
    setSelectedProduct(p);
    setShowDetails(true);
  };

  const deleteProduct = (id) => {
    const ok = confirm("ŸáŸÑ ÿ£ŸÜÿ™ ŸÖÿ™ÿ£ŸÉÿØ ŸÖŸÜ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿü");
    if (!ok) return;
    setProducts((prev) => prev.filter((p) => p.id !== id));
  };

  const handleAddProduct = () => {
    if (!newProduct.name || !newProduct.price) {
      alert("ÿßŸÑÿßÿ≥ŸÖ ŸàÿßŸÑÿ≥ÿπÿ± ŸÖÿ∑ŸÑŸàÿ®ÿßŸÜ ÿπŸÑŸâ ÿßŸÑÿ£ŸÇŸÑ");
      return;
    }

    const id = Date.now();
    setProducts((prev) => [
      ...prev,
      {
        id,
        ...newProduct,
        purchasePrice: Number(newProduct.purchasePrice) || 0,
        price: Number(newProduct.price) || 0,
        quantity: Number(newProduct.quantity) || 0,
        minQty: Number(newProduct.minQty) || 0,
      },
    ]);

    setShowAddModal(false);
    setNewProduct({
      name: "",
      sku: "",
      category: "",
      company: "",
      purchasePrice: "",
      price: "",
      quantity: "",
      minQty: 5,
      expiryDate: "",
    });
  };

  const printProducts = () => {
    const w = window.open("", "", "width=900,height=700");

    w.document.write(`
      <html dir="rtl" lang="ar">
      <head>
        <title>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™</title>
        <style>
          body { font-family:'Tajawal',sans-serif; padding:20px; }
          h2 { text-align:center; }
          table { width:100%; border-collapse:collapse; margin-top:20px; }
          th, td { border:1px solid #ddd; padding:6px; font-size:12px; text-align:right; }
          th { background:#f1f5f9; }
        </style>
      </head>
      <body>
        <h2>üìÑ ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™</h2>
        <table>
          <thead>
            <tr>
              <th>ÿßŸÑÿßÿ≥ŸÖ</th>
              <th>ÿßŸÑŸÉŸàÿØ</th>
              <th>ÿßŸÑŸÅÿ¶ÿ©</th>
              <th>ÿßŸÑÿ¥ÿ±ŸÉÿ©</th>
              <th>ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°</th>
              <th>ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ</th>
              <th>ÿßŸÑŸÉŸÖŸäÿ©</th>
              <th>ÿ±ÿ®ÿ≠/Ÿàÿ≠ÿØÿ©</th>
              <th>ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑÿ±ÿ®ÿ≠</th>
              <th>ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©</th>
            </tr>
          </thead>
          <tbody>
            ${products
              .map((p) => {
                const unitProfit =
                  (p.price || 0) - (p.purchasePrice || 0);
                const totalProfit = unitProfit * (p.quantity || 0);
                return `
                  <tr>
                    <td>${p.name}</td>
                    <td>${p.sku}</td>
                    <td>${p.category}</td>
                    <td>${p.company}</td>
                    <td>${p.purchasePrice || 0}</td>
                    <td>${p.price || 0}</td>
                    <td>${p.quantity || 0}</td>
                    <td>${unitProfit.toFixed(2)}</td>
                    <td>${totalProfit.toFixed(2)}</td>
                    <td>${p.expiryDate || ""}</td>
                  </tr>
                `;
              })
              .join("")}
          </tbody>
        </table>
        <script>window.print()</script>
      </body>
      </html>
    `);

    w.document.close();
  };

  return (
    <Layout user={user} title="ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™">
      <div dir="rtl" className="space-y-6">

        {/* ÿßŸÑÿπŸÜŸàÿßŸÜ + ÿßŸÑÿ£ÿ≤ÿ±ÿßÿ± */}
        <div className="flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
          <h1 className="text-xl font-bold text-gray-800">üíä ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™</h1>

          <div className="flex flex-wrap gap-2">
            <button
              onClick={() => setShowAddModal(true)}
              className="px-4 py-2 text-sm text-white bg-green-600 rounded-lg hover:bg-green-700"
            >
              ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨
            </button>

            <button
              onClick={printProducts}
              className="px-4 py-2 text-sm text-white bg-purple-600 rounded-lg hover:bg-purple-700"
            >
              üñ®Ô∏è ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™
            </button>
          </div>
        </div>

        {/* ÿßŸÑŸÅŸÑÿßÿ™ÿ± ŸàÿßŸÑÿ®ÿ≠ÿ´ */}
        <div className="p-4 space-y-4 bg-white border shadow rounded-xl">
          <input
            type="text"
            placeholder="ÿßÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖŸÜÿ™ÿ¨‚Ä¶"
            className="w-full p-3 text-sm border rounded-lg"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />

          <div className="flex flex-wrap items-center gap-3 text-sm">

            <select
              className="p-2 border rounded-lg"
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value)}
            >
              {categories.map((cat) => (
                <option key={cat} value={cat}>
                  {cat === "all" ? "ŸÉŸÑ ÿßŸÑŸÅÿ¶ÿßÿ™" : cat}
                </option>
              ))}
            </select>

            <select
              className="p-2 border rounded-lg"
              value={companyFilter}
              onChange={(e) => setCompanyFilter(e.target.value)}
            >
              {companies.map((c) => (
                <option key={c} value={c}>
                  {c === "all" ? "ŸÉŸÑ ÿßŸÑÿ¥ÿ±ŸÉÿßÿ™" : c}
                </option>
              ))}
            </select>

            <select
              className="p-2 border rounded-lg"
              value={sortByName}
              onChange={(e) => setSortByName(e.target.value)}
            >
              <option value="asc">ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÜÿ™ÿ¨ (ÿ™ÿµÿßÿπÿØŸä)</option>
              <option value="desc">ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÜÿ™ÿ¨ (ÿ™ŸÜÿßÿ≤ŸÑŸä)</option>
            </select>

            <label className="flex items-center gap-1">
              <input
                type="checkbox"
                checked={filterLowStock}
                onChange={() => setFilterLowStock(!filterLowStock)}
              />
              <span>ŸÉŸÖŸäÿ© ŸÖŸÜÿÆŸÅÿ∂ÿ©</span>
            </label>

            <label className="flex items-center gap-1">
              <input
                type="checkbox"
                checked={filterNearExpiry}
                onChange={() => setFilterNearExpiry(!filterNearExpiry)}
              />
              <span>ŸÇÿ±ÿ® ÿßŸÜÿ™Ÿáÿßÿ° ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©</span>
            </label>

            <label className="flex items-center gap-1">
              <input
                type="checkbox"
                checked={filterExpired}
                onChange={() => setFilterExpired(!filterExpired)}
              />
              <span>ŸÖŸÜÿ™ŸáŸä ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©</span>
            </label>
          </div>
        </div>

        {/* ÿ¨ÿØŸàŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ */}
        <div className="overflow-x-auto bg-white border shadow rounded-xl">
          <table className="w-full text-sm text-right min-w-[900px]">
            <thead className="bg-gray-50">
              <tr>
                <th className="p-3">ÿßŸÑÿßÿ≥ŸÖ</th>
                <th className="p-3">ÿßŸÑŸÉŸàÿØ</th>
                <th className="p-3">ÿßŸÑŸÅÿ¶ÿ©</th>
                <th className="p-3">ÿßŸÑÿ¥ÿ±ŸÉÿ©</th>
                <th className="p-3">ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°</th>
                <th className="p-3">ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ</th>
                <th className="p-3">ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ</th>
                <th className="p-3">ÿ±ÿ®ÿ≠/Ÿàÿ≠ÿØÿ©</th>
                <th className="p-3">ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑÿ±ÿ®ÿ≠</th>
                <th className="p-3">ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©</th>
                <th className="p-3 text-center">ÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™</th>
                <th className="p-3 text-center">ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
              </tr>
            </thead>

            <tbody>
              {filteredProducts.map((p) => {
                const warnings = getWarnings(p);
                const unitProfit =
                  (p.price || 0) - (p.purchasePrice || 0);
                const totalProfit = unitProfit * (p.quantity || 0);

                let expiryText = p.expiryDate || "-";
                if (
                  warnings.includes("‚ùå ÿßŸÑŸÖŸÜÿ™ÿ¨ ŸÖŸÜÿ™ŸáŸä ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿ©!")
                ) {
                  expiryText = "ŸÖŸÜÿ™ŸáŸä";
                }

                return (
                  <tr key={p.id} className="border-t hover:bg-gray-50">
                    <td className="p-3">{p.name}</td>
                    <td className="p-3">{p.sku}</td>
                    <td className="p-3">{p.category}</td>
                    <td className="p-3">{p.company}</td>
                    <td className="p-3">{p.purchasePrice || 0} ÿ±.ÿ≥</td>
                    <td className="p-3">{p.price || 0} ÿ±.ÿ≥</td>
                    <td
                      className={`p-3 ${
                        p.quantity <= p.minQty
                          ? "text-red-600 font-bold"
                          : ""
                      }`}
                    >
                      {p.quantity}
                    </td>
                    <td className="p-3">
                      {unitProfit.toFixed(2)} ÿ±.ÿ≥
                    </td>
                    <td className="p-3">
                      {totalProfit.toFixed(2)} ÿ±.ÿ≥
                    </td>
                    <td className="p-3">{expiryText}</td>

                    <td className="p-3 text-center">
                      <WarningIndicator warnings={warnings} />
                    </td>

                    <td className="p-3 text-center">
                      <div className="flex justify-center gap-2">
                        <button
                          onClick={() => openDetails(p)}
                          className="px-3 py-1 text-xs text-white bg-indigo-600 rounded-lg hover:bg-indigo-700"
                        >
                          üîç ÿπÿ±ÿ∂
                        </button>

                        <button
                          onClick={() =>
                            router.push(`/inventory?product=${p.id}`)
                          }
                          className="px-3 py-1 text-xs text-white bg-blue-600 rounded-lg hover:bg-blue-700"
                        >
                          üì¶ ŸÖÿÆÿ≤ŸàŸÜ
                        </button>

                        <button
                          onClick={() =>
                            router.push(`/products/edit/${p.id}`)
                          }
                          className="px-3 py-1 text-xs text-white rounded-lg bg-amber-600 hover:bg-amber-700"
                        >
                          ‚úèÔ∏è ÿ™ÿπÿØŸäŸÑ
                        </button>

                        <button
                          onClick={() => deleteProduct(p.id)}
                          className="px-3 py-1 text-xs text-white bg-red-600 rounded-lg hover:bg-red-700"
                        >
                          üóëÔ∏è ÿ≠ÿ∞ŸÅ
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}

              {!filteredProducts.length && (
                <tr>
                  <td colSpan={12} className="p-4 text-center text-gray-400">
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÜÿ™ÿßÿ¶ÿ¨ ŸÖÿ∑ÿßÿ®ŸÇÿ©‚Ä¶
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/* ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ */}
        {showDetails && selectedProduct && (
          <Modal
            title="ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨"
            onClose={() => setShowDetails(false)}
            onConfirm={() => setShowDetails(false)}
            confirmLabel="ÿ•ÿ∫ŸÑÿßŸÇ"
          >
            <div className="space-y-2 text-sm" dir="rtl">
              <p><strong>ÿßŸÑÿßÿ≥ŸÖ:</strong> {selectedProduct.name}</p>
              <p><strong>ÿßŸÑŸÉŸàÿØ:</strong> {selectedProduct.sku}</p>
              <p><strong>ÿßŸÑŸÅÿ¶ÿ©:</strong> {selectedProduct.category}</p>
              <p><strong>ÿßŸÑÿ¥ÿ±ŸÉÿ©:</strong> {selectedProduct.company}</p>
              <p><strong>ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°:</strong> {selectedProduct.purchasePrice || 0} ÿ±.ÿ≥</p>
              <p><strong>ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ:</strong> {selectedProduct.price || 0} ÿ±.ÿ≥</p>
              <p><strong>ÿßŸÑŸÉŸÖŸäÿ©:</strong> {selectedProduct.quantity}</p>
              <p><strong>ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ÿØŸÜŸâ:</strong> {selectedProduct.minQty}</p>
              <p><strong>ÿ™ÿßÿ±ŸäÿÆ ÿßŸÑÿßŸÜÿ™Ÿáÿßÿ°:</strong> {selectedProduct.expiryDate}</p>

              <div className="mt-3">
                <strong>ÿßŸÑÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™:</strong>
                {getWarnings(selectedProduct).length ? (
                  <ul className="pr-4 mt-1 text-xs text-red-600 list-disc">
                    {getWarnings(selectedProduct).map((w, i) => (
                      <li key={i}>{w}</li>
                    ))}
                  </ul>
                ) : (
                  <p className="mt-1 text-xs text-green-600">
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ™ÿ≠ÿ∞Ÿäÿ±ÿßÿ™.
                  </p>
                )}
              </div>
            </div>
          </Modal>
        )}

        {/* ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ */}
        {showAddModal && (
          <Modal
            title="ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ ÿ¨ÿØŸäÿØ"
            onClose={() => setShowAddModal(false)}
            onConfirm={handleAddProduct}
            confirmLabel="ÿ•ÿ∂ÿßŸÅÿ©"
          >
            <div className="space-y-3 text-sm" dir="rtl">
              <input
                type="text"
                className="w-full p-2 border rounded"
                placeholder="ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÜÿ™ÿ¨"
                value={newProduct.name}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, name: e.target.value })
                }
              />
              <input
                type="text"
                className="w-full p-2 border rounded"
                placeholder="ÿßŸÑŸÉŸàÿØ SKU"
                value={newProduct.sku}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, sku: e.target.value })
                }
              />
              <input
                type="text"
                className="w-full p-2 border rounded"
                placeholder="ÿßŸÑŸÅÿ¶ÿ©"
                value={newProduct.category}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, category: e.target.value })
                }
              />
              <input
                type="text"
                className="w-full p-2 border rounded"
                placeholder="ÿßŸÑÿ¥ÿ±ŸÉÿ©"
                value={newProduct.company}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, company: e.target.value })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded"
                placeholder="ÿ≥ÿπÿ± ÿßŸÑÿ¥ÿ±ÿßÿ°"
                value={newProduct.purchasePrice}
                onChange={(e) =>
                  setNewProduct({
                    ...newProduct,
                    purchasePrice: e.target.value,
                  })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded"
                placeholder="ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ"
                value={newProduct.price}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, price: e.target.value })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded"
                placeholder="ÿßŸÑŸÉŸÖŸäÿ©"
                value={newProduct.quantity}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, quantity: e.target.value })
                }
              />
              <input
                type="number"
                className="w-full p-2 border rounded"
                placeholder="ÿßŸÑÿ≠ÿØ ÿßŸÑÿ£ÿØŸÜŸâ"
                value={newProduct.minQty}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, minQty: e.target.value })
                }
              />
              <input
                type="date"
                className="w-full p-2 border rounded"
                value={newProduct.expiryDate}
                onChange={(e) =>
                  setNewProduct({ ...newProduct, expiryDate: e.target.value })
                }
              />
            </div>
          </Modal>
        )}
      </div>
    </Layout>
  );
}














